p {
    margin: 0;
}

.signIn input[type=text], select {

    border: 0;
    border-bottom: 2px solid #ccc;
    border-radius: 0;
}

.input input[type=text], select {

    border: 0;
    border-bottom: 2px solid #ccc;
    border-radius: 0;


}

.lightgrey {
    color: grey;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    padding: 20px;
    color: white;
    border-bottom: 3px solid darkred;
    text-align: left;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
}

.App-header-background {
    background: url(../img/duleone.jpg) center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;

    height: 118px;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: .5;
    z-index: -1;
}

.App-header-left {
    display: inline-block;
    width: 15%;
    text-align: left;
}

.App-header-left img {
    height: 75px;
    display: inline-block;
}

.App-header-center {
    display: inline-block;
    width: 70%;
    text-align: center;
    top: 0;
    margin: 0;
    vertical-align: 140%;
}

.App-header-center h2 {
    margin: 0 auto;
}

.App-header-right {
    display: inline-block;
    color: white;
    right: 0;
    top: 0;
    margin-left: 10%;
    vertical-align: top;
    position: fixed;
}

.show {

}

.hide {
    margin-right: -300px;
}

.profileInfo {
    height: 150px;
    background: transparent;
    position: absolute;
    top: 0;
    left: 0;

}

.profilePic {
    height: 150px;
    background: url(../img/outside_bw.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    opacity: .5;
    z-index: -1;
}

.profile {
    display: inline-block;
    margin: 15px;
    font-size: 9px;
    vertical-align: middle;
}

.hamburger {
    margin-top: 10px;
    z-index: 3;
}

.hamburger img {
    text-align: right;
    margin-right: 20px;
    margin-top: 8px;
    font-size: 8px;
    height: 15px;
}

.menuContainer {
    position: fixed;
    top: 0;
    right: 0;
    background-color: black;
    height: 500px;
    width: 300px;
    z-index: -1;
}

.menu {
    margin-top: 50px;
    margin-right: 30px;
    margin-left: 80px;
}

.menu ul {
    list-style: none;
    padding: 0px;
    margin-top: 60px;
}

.menu li {
    margin-top: 20px;
    font-weight: 300;
}

.signOut {
    color: white;
    border-top: 1px solid darkred;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60px;
    text-align: center;
    padding-top: 30px;
    background: #222;

}

.signOutBtn {
    border: none;
    background: none;
    font-size: inherit;
    color: white;
}




.App-intro {
    font-size: large;
}


.window {
    background-color: gray;
}

.signIn {
    background-color: white;
    border-bottom: 1px solid rgba(0,0,0, .3);
    position: fixed;
    top: 158px;
    left: 0;
    right: 0;
}

.userName {
    background-color: lightgrey;
    display: inline-block;
    margin: 10px;
    text-align: left;
}

.joinRoom {
    display: inline-block;
    margin: 10px;
    margin-right: 30px;

}

.chatList {
    display: inline-block;
    margin: 10px;
    margin-left: 30px;

}


.signIn div {
    display: inline-block;
}

.signInInput {
    width: 150px;
    border-radius: 5px;
}

.item {
    display: block;
    font-size: 10px;
    text-align: left;
}

.status {
    background-color: black;
    position: fixed;
    top: 120px;
    left: 0;
    right: 0;
    z-index: 0;
}

.userNameRight {
    background-color: black;
    color: white;
    padding: 10px;
    display: inline-block;
    margin-left: 100px;

}

.userNameLeft {
    background-color: black;
    color: white;
    padding: 10px;
    display: inline-block;
    margin-right: 100px;
}

.spacer {
    height: 200px;
}

.conversation {
    height: 600px;
    background-color: white;
    padding: 10px;
    overflow: scroll;
    text-align: left;
    position: fixed;
    bottom: 150px;
    right: 0;
    left: 0;
    z-index: -1;
}

.messageList {
    text-align: left;
    list-style: none;
    margin-top: 10px;
    padding: 0;
}

.messageItemHome {
    margin: 10px;
    border: 2px solid dimgray;
    border-radius: 20px;
    padding: 10px;
    color: white;
    background-color: darkred;
    width: 45%;
    margin-left: 45%;
    margin-right: 5%;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.messageItemAway {
    margin: 10px;
    border: 2px solid darkred;
    border-radius: 20px;
    padding: 10px;
    background-color: lightgrey;
    width: 45%;
    margin-left: 5%;

}


.input {
    height: 50px;
    background-color: white;
    position: fixed;
    bottom: 100px;
    left: 0;
    right: 0;
    border-top: 2px solid rgba(0,0,0,.2);

}

.typeMessage {
    margin: 5px 5px 5px 5px;
    height: 25px;
    width: 75%;
    border-radius: 5px;
}

.send {
    margin: 5px 5px 5px 5px;
    height: 25px;
    width: 50px;
    color: darkred;
    background: white;
    border-radius: 20px;
    border: 1px solid darkred;
    -webkit-appearance: none;
}

.footer {
    height: 97px;
    background-color: black;
    border-top: 3px solid darkred;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

.signOutDiv {
    text-align: right;
}


.link {
    text-decoration: none;
    color: white;

}

.btn-danger {
    background-color: darkred;
    color: white;
    border: 2px solid black;
    border-radius: 3px;
    z-index: 10;
}

.btn-primary {
    background-color: darkred;
    color: white;
    border: 2px solid black;
    border-radius: 3px;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    font-weight: 100;
}

h1, h2, h3, p {
    font-weight: 100;
}

.form-group {
    margin-bottom: 40px;
    height: 160px;

}

.form-control {
    border: 1px solid lightgrey;
}

.form-control {
    display: block;
    margin: 30px auto;
    border: 0;
    border-bottom: 1px solid black;

}

.form-inline {
    margin-left: 15%;
    margin-top: 200px;
    text-align: center;
    width: 70%;
}

.form-inline h2 {
    color: #222;
}

.signLink1 {
    color: white;
    padding: 10px 30px;
    background-color: darkred;
    text-decoration: none;
    border-radius: 20px;
    width: 100px;
    margin: 0 auto;
}

.signLink {
    color: white;
    padding: 20px 20px;
    background-color: black;
    text-decoration: none;
    margin: 78px auto 0 auto;

}

.ready {
    margin: 88px auto 0;
    width: 156px;
}




@media only screen and (max-width: 480px) {

    .App-header {
        padding: 10px;
        height: 30px;
    }

    .App-header-background {
        height: 50px;
    }

    .App-header-center {
        font-size: 9px;
        width: 70%;
    }


    .App-header-left {
        width: 15%;
    }


    .App-header-left img {
        height: 25px;
        margin-top: 5px;
        margin-left: 5px;
    }

    .status {
        top: 53px;
        height: 30px;
    }

    .status p {
        font-size: 9px;
        padding: 0;
    }

    .status div {
        margin: 0;
    }


    .userNameLeft {
        width: 45%;
        padding: 0;
        text-align: left;
    }

    .userNameRight {
        width: 45%;
        padding: 0;
        text-align: right;
    }

    .signIn {
        top: 83px;
    }

    .signIn div {
        display: inline-block;
    }

    .already {
        width: 156px;
        margin: 0 auto;
    }

    .already p {
        margin-bottom: 30px;
    }

    .form-inline {
        margin-top: 130px;
    }

    .signInInput {
        width: 100px;
    }

    .signLink {
        margin-top: 78px;
    }

    .item {
        font-size: 8px;
    }

    .joinRoom {
        margin-right: 20px;
    }

    .chatList {
        margin-left: 20px;
        border-radius: 5px;
    }

    #great-names {
        width: 100px;
        background: white;
        -webkit-appearance: none;
    }

    .conversation {
        height: 80%;
        position: fixed;
        bottom: 50px;
        left: 0;
        right: 0;
        overflow: auto;
        z-index: -1;
    }

    .typeMessage {
        width: 60%;
    }

    .input {
        bottom: 0;
    }

    .footer {
        display: none;
    }


}


@media only screen and (orientation: landscape) and (max-width: 736px) {

    .App-header {
        padding: 10px;
        height: 40px;
    }

    .App-header-center {
        font-size: 12px;
    }


    .App-header-left img {
        height: 30px;
        margin-top: 5px;
        margin-left: 10px;
    }

    .status {
        top: 63px;
        height: 30px;
    }

    .status p {
        font-size: 9px;
        padding: 0;
    }

    .status div {
        margin: 0;
    }

    .userNameLeft {
        width: 50%;
        padding: 0;
        text-align: left;
    }

    .userNameRight {
        width: 40%;
        padding: 0;
        text-align: right;
    }

    .signIn {
        top: 80px;
    }

    .signIn div {
        display: inline-block;
    }

    .signInInput {
        width: 120px;
    }

    .item {
        font-size: 8px;
    }

    .conversation {
        height: 55%;
        position: fixed;
        bottom: 40px;
        left: 0;
        right: 0;
        overflow: auto;
        z-index: -1;
    }

    .typeMessage {
        width: 60%;
    }

    .input {
        bottom: 0;
    }

    .footer {
        display: none;
    }

    .form-inline {
        margin-top: 100px;
    }

}





